import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"

import { Layout } from "../components/Layout"
import { Page } from "../components/Page"
import { Title } from "../components/Title"
import { Quote } from "../components/Quote"
import { Row, Column } from "../style"

export default function MappingPage() {
  const data = useStaticQuery(
    graphql`
      query {
        heroImage: file(relativePath: { eq: "cafe-2.jpg" }) {
          childImageSharp {
            fluid(quality: 70, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        cafe: file(relativePath: { eq: "cafe.jpg" }) {
          childImageSharp {
            fluid(quality: 70, maxWidth: 250) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `
  )

  return (
    <Layout>
      <Page metaTitle="Mapping" heroImage={data.heroImage}>
        <Title>Mapping</Title>
        <Row>
          <Column span="6">Column</Column>
          <Column span="6">
            <Img fluid={data.cafe.childImageSharp.fluid} />
          </Column>
        </Row>
        <Quote>Quote</Quote>
      </Page>
    </Layout>
  )
}
